.card {
  padding: 16px;
  text-align: center;
  display: flex;
  border-radius: 8px;
  flex-direction: column;
  gap: 4px;
  background-color: #ffd50074;
  cursor: pointer;
  margin: 1em;
  transition: all 0.3s ease-in-out;
}

.card-list__container h2 {
  text-align: center;
}

.card-list__main {
  display: flex;
  width: 50%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem auto;
  max-width: 1220px;
}

.card img {
  width: 120px;
  height: 120px;
}

.card:hover {
  transform: scale(1.07);
}

.card hr {
  border: none;
  height: 1px;
  background-color: #10162f;
}

@media (max-width: 768px) {
  .card img {
    width: 80px;
    height: 80px;
  }

  .card-list-container {
    width: 100%;
  }
}
