@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&display=swap);
.card {
  padding: 16px;
  text-align: center;
  display: flex;
  border-radius: 8px;
  flex-direction: column;
  grid-gap: 4px;
  gap: 4px;
  background-color: #ffd50074;
  cursor: pointer;
  margin: 1em;
  transition: all 0.3s ease-in-out;
}

.card-list__container h2 {
  text-align: center;
}

.card-list__main {
  display: flex;
  width: 50%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem auto;
  max-width: 1220px;
}

.card img {
  width: 120px;
  height: 120px;
}

.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card hr {
  border: none;
  height: 1px;
  background-color: #10162f;
}

@media (max-width: 768px) {
  .card img {
    width: 80px;
    height: 80px;
  }

  .card-list-container {
    width: 100%;
  }
}

.header-container {
  background-color: #10162f;
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-container__logo {
  display: flex;
  align-items: center;
  color: white;
}

.header-container__logo h1 {
  margin-top: 40px;
}

.header-container__logo img {
  width: 120px;
  height: 100px;
}

.header-container__score-info {
  color: white;
  margin-right: 2rem;
  padding: 0;
}

.header-container__score-info li {
  list-style: none;
}

.la-star {
  color: yellow;
}

@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
  }

  .header-container__logo {
    flex-direction: column;
  }

  .header-container__logo h1 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .header-container__score-info {
    display: flex;
    margin-right: 0;
  }

  .header-container__score-info li {
    align-self: center;
    padding-right: 1rem;
  }
}

.loader-container {
  display: flex;
  width: 50%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem auto;
  max-width: 1220px;
}

@media (max-width: 768px) {
  .loader-container {
    width: 100%;
  }
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #3a10e5;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #3a10e5 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  8. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

body,
html {
  font-family: 'Courier Prime', monospace, Arial;
}

